.toggle-menu {
  position: absolute;
  z-index: 99;
  right: 0;
  top: 0;
  width: 44px;
  height: 44px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.line {
  width: 44px;
  height: 3px;
  border-radius: 3px;
  background-color: #000000;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  position: absolute;
  z-index: 1000;
  top: 7px;
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(-45deg) translate(-4px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  position: absolute;
  z-index: 1000;
  top: 17px;
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(45deg) translate(-4px, -4px);
}

.toggle .toggle-menu {
  background-color: transparent;
}

