  }
}
.accordion-card__content {
  max-height: 0;
  overflow: hidden;
  padding-top: 20px;
  transition: max-height 0.35s;
}
.accordion-card__text {
  font-size: 12px;
  font-weight: 300;
  line-height: 1.5;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: flex-start;
  gap: 20px;
}
@media (min-width: 640px) {
  .accordion-card__text {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .accordion-card__text {
    font-size: 16px;
  }
}
.accordion-card__list {
  margin: 0;
  padding-left: 25px;
}

.accordion-card input:checked ~ .accordion-card__content {
  max-height: 100vh;
}

.accordion-card__title::after {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: 3em;
  height: 3em;
  line-height: 3;
  text-align: center;
  transition: all 0.35s;
}

.accordion-card input[type=checkbox] + label::after {
  position: absolute;
  content: "";
  background-image: url("../img/icons/btn.svg");
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-position: center;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 1px solid #000000;
}

.accordion-card input[type=checkbox]:checked + label::after {
  transform: rotate(315deg);
}

.accordion-card input[type=radio]:checked + label::after {
  transform: rotateX(180deg);
}

